<template>
  <v-container fluid >
    <v-row >
      <v-col
        v-for="item in layout"
        :key="item.i"
        :cols="calculateCols(item)"
        :sm="calculateCols(item)"
        :md="item.w"
        class="w-auto h-auto"
        :offset="item.i === 'chart4' ? 6: 0"
      >
        <vue-draggable-resizable
          v-if="item.i === 'table3'"
          :w="computedWidth(98)"
          :h="computedHeight(50)"
          :x="item.x"
          :y="item.y"
          :z="10"
          :draggable="isDraggable"
          :resizable="isDraggable"
          class="grid-item-background"          
        >
        <v-card>
          <v-card-title>
            <v-row dense justify="center">
              
              <v-col :cols="6" class="text-left" >
                <span class="clock">최종 데이터 수신 : {{ dataCollectionTime }}</span>
              </v-col>
              <v-col :cols="6" class="d-flex justify-end ">
                <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" :loading="loading" @click="getHistoryList"><v-icon>mdi-refresh</v-icon> 새로고침</v-btn></div>
                <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" :loading="loading" @click="downloadExcel(3)"><v-icon style="padding-right:5px">mdi-tray-arrow-down</v-icon> 3개월 </v-btn></div>
                <!-- <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" :loading="loading" @click="downloadExcel(1)"><v-icon style="padding-right:5px">mdi-tray-arrow-down</v-icon> 분기별</v-btn></div> -->
              </v-col>
            </v-row>
          </v-card-title>
            <v-data-table
              dense
              divider
              fixed-header
              :headers="headers"
              :items="items"
              :items-per-page="50"
              :footer-props="{ itemsPerPageOptions: [10,20,30,50,'All'] }"
              :height="computedHeight(80)"
            >
              <template v-slot:[`item.no`]="{ index }">
                <span>{{ index + 1 }}</span>
              </template>  
              <!-- <template v-slot:[`item.STATUS`]="{ item }">
                <v-chip
                    :color=" item.STATUS == 1 ? 'error' : item.STATUS == 4 ? 'success' : item.STATUS == 5 ? 'warning' : item.STATUS == 0 ? '#333' : '#000' "
                    label
                    small
                    class="pd-5"
                  >
                  
                 
                  </v-chip>
              </template>  -->
            </v-data-table>
          </v-card>
        </vue-draggable-resizable>        
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapMutations} from "vuex";
import moment from 'moment';
import XLSX from 'xlsx/xlsx.js';

export default {  
  data() {
    return {
      loading: false,
      isDraggable: false,
      dataCollectionTime: '00:00:00',
      layout: [
        { x: 0, y: 0, w: 12, h: 8, i: "table3" },
        { x: 0, y: 4, w: 12, h: 8, i: "chart1" },
      ],
      headers: [
        { text: "No.", value: "no" , align: "center",width:"50"},
        { text: "데이터수집시간", value: "createdAt" ,width:"20%"},
        { text: "일련번호", value: "DEVICE_ID" ,width:"20%"},
        { text: "시작온도", value: "ON_TEMP" ,width:"15%"},
        { text: "현재온도", value: "CURR_TEMP" ,width:"15%"},
        { text: "종료온도", value: "OFF_TEMP" ,width:"15%"},
        { text: "상태" ,value:"STATUS_TEXT" , widht:"10%"}
      ],
      items: [],
    };
  },
  computed: {
    //...mapState('wssMsg',['msg']),
  },
  watch: {
    msg(newVal){
      if (newVal) {
        this.updateCharts();
      }
    },
  },
  methods: {    
    ...mapMutations(['setShowMsg']),
    calculateCols(item) {
      // 모바일 환경에서 모든 그리드 아이템이 전체 너비를 차지하도록 조정
      return this.$vuetify.breakpoint.mdAndUp ? item.w : 12;
    },
    // 현재일시 기준 -1일로 from 파라메터 만들어  DynamoDBService.getHistoryList 조회 호출
    getHistoryList() {
      this.items = [];
      this.loading = true;
      const from = moment().subtract(1, 'days').format('YYYY-MM-DD');
      const to = moment().format('YYYY-MM-DD');
      DynamoDBService.getHistoryList(from, to).then((res) => {
        this.items = res;
        //console.log(this.items);
        this.loading = false;
      })
      .catch((err) => {
        console.log(err);
        this.loading = false;
      }).finally(() => {
        //this.updateCharts();  
        this.dataCollectionTime = this.items[0].createdAt;
      });
    },
    computedWidth(p){
      // 여기에서 화면 크기에 맞게 값을 계산하고 반환하세요
      const screenWidth = document.querySelector(".v-main__wrap").clientWidth;
      // console.log("@ computedWidth - screenWidth : ",screenWidth);
      // console.log("@ computedWidth : ",Math.floor(screenWidth * (p*0.01)));
      return Math.floor(screenWidth * (p*0.01)); // 예 50: 화면 너비의 50%를 반환합니다
    },
    computedHeight(p) {
      // 여기에서 화면 크기에 맞게 값을 계산하고 반환하세요
      const screenHeight = document.querySelector(".v-main__wrap").clientHeight;
      // console.log("@ computedHeight - screenHeight : ",screenHeight);
      // console.log("@ computedHeight : ",Math.floor(screenHeight * (p*0.01)));
      return Math.floor(screenHeight * (p*0.01)); // 예 50: 화면 높이의 50%를 반환합니다
    },
    showErrorPopup(errMsg) {
      errMsg = "신림동 지점\n스마일 기사\n010-1111-2222\n\n" + errMsg;
      let param = {isErrorMsg:true,color:'error',msg:errMsg}
      this.setShowMsg(param);
    },
    downloadExcel(val){
      this.loading = true;
      let fileName = '';
      let from, to = '';
      if(val > 1){
        fileName = '3개월 데이터';
        from = moment().subtract(2, "months").format('YYYY-MM-DD');
        to = moment().format('YYYY-MM-DD');
      }else{
        fileName = '분기별 데이터';
        from = moment().subtract(1, "year").format('YYYY-MM-DD');
        to = moment().format('YYYY-MM-DD');
      }
      DynamoDBService.getHistoryList(from, to).then((res) => {
        const workBook = XLSX.utils.book_new();
        const workSheet = XLSX.utils.json_to_sheet(res);
        XLSX.utils.book_append_sheet(workBook, workSheet, fileName);
        XLSX.writeFile(workBook, fileName+'.xlsx');

        this.loading = false;
      })
      .catch((err) => {
        console.log(err);
        this.loading = false;
      });

      
    },
  },
  mounted() {
    this.getHistoryList();
     this.intvId = setInterval(() => {
       this.getHistoryList();
     }, 10000);
  },
};
</script>
<style scoped>
.vue-grid-item  {
  touch-action: none;
  /* border: 1px solid goldenrod; */
}
.grid-item-background {
  background-color: #1e1e1e;
}

.clock {
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;
  font-weight: 500;  
  text-align: right;  
}
</style>