
<template>
  <v-container fluid >
    <v-row >
      <v-col
        v-for="item in layout"
        :key="item.i"
        :cols="calculateCols(item)"
        :sm="calculateCols(item)"
        :md="item.w"
        class="w-auto h-auto"
        :offset="item.i === 'chart4' ? 6: 0"
      >
        <vue-draggable-resizable
          v-if="item.i === 'table3'"
          :w="computedWidth(98)"
          :h="computedHeight(50)"
          :x="item.x"
          :y="item.y"
          :z="10"
          :draggable="isDraggable"
          :resizable="isDraggable"
          class="grid-item-background"          
        >
        <v-card>
          <v-card-title>
            <v-col :cols="6" class="text-left" >
                <span class="clock">최종 데이터 수신 : {{ dataCollectionTime }}</span>
              </v-col>
              <v-col :cols="6" class="d-flex justify-end ">
                <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" @click="getHistoryList"><v-icon>mdi-refresh</v-icon> 새로고침</v-btn></div>
                <div class="ml-2"><v-btn color="success darken-1" class="subtitle-1" @click="saveHistory"><v-icon style="padding-right:5px">mdi-content-save</v-icon> 저장</v-btn></div>
                <!-- <div class="ml-2"><v-btn color="warning darken-1" class="subtitle-1" @click="downloadExcel(3)"><v-icon style="padding-right:5px">mdi-tray-arrow-down</v-icon> 3개월 </v-btn></div>
                <div class="ml-2"><v-btn color="warning darken-1" class="subtitle-1" @click="downloadExcel(1)"><v-icon style="padding-right:5px">mdi-tray-arrow-down</v-icon> 분기별</v-btn></div> -->
              </v-col>
          </v-card-title>
            <v-data-table
              dense
              divider
              fixed-header
              no-data-text="수신된 알림이 없습니다."
              :headers="headers"
              :items="items"
              :items-per-page="10"
              :footer-props="{ itemsPerPageOptions: [10,20,30,50,'All'] }"
              :height="computedHeight(80)"
              show-select
              v-model="selected"
              item-key="SEQ"
            >
              <template v-slot:[`item.no`]="{ index }">
                <span>{{ index + 1 }}</span>
              </template>  
              <template  v-slot:[`item.STATUS`]="{ item }">
                <v-chip
                    :color=" item.STATUS == 1 ? 'error' : item.STATUS == 4 ? 'success' : item.STATUS == 5 ? 'warning' : item.STATUS == 0 ? '#333' : '#000' "
                    label
                    small
                    @click="showErrorPopup"
                    class="pd-5"
                  >
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-if="item.STATUS == 0">OFF</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 1">ON</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 2">단선</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 3">과전류</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 4">온도센서</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 5">위치인식</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 6">통신오류</p>
                  </v-chip>
              </template> 
              <template v-slot:[`item.DESC`]="{ item }">
                <v-text-field v-model="item.DESC" :disabled="!isAdmin" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field>                
              </template>
            </v-data-table>
          </v-card>
        </vue-draggable-resizable>        
      </v-col>
    </v-row>   
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="80"
        width="10"
        color="primary lighten-2"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapState, mapMutations} from "vuex";
import moment from 'moment';
import XLSX from 'xlsx/xlsx.js';

export default {  
  data() {
    return {
     loading: false,
     selected: [],
      isDraggable: false,
      dataCollectionTime: '00:00:00',
      layout: [
        { x: 0, y: 0, w: 12, h: 8, i: "table3" },
        { x: 0, y: 4, w: 12, h: 8, i: "chart1" },
      ],
      headers: [
        { text: "No.", value: "no" , align: "center",width:"50"},
        { text: "데이터수집시간", value: "createdAt" ,width:"20%"},
        { text: "일련번호", value: "DEVICE_ID" ,width:"20%"},
        // { text: "시작온도", value: "ON_TEMP" ,width:"15%"},
        // { text: "현재온도", value: "CURR_TEMP" ,width:"15%"},
        // { text: "종료온도", value: "OFF_TEMP" ,width:"15%"},
        { text: "상태" ,value:"STATUS" , widht:"10%"},
        { text: "조치내용" ,value:"DESC" , widht:"10%"}
      ],
      items: [],
      newItems: [],
      page: 1,
      pageCount: 10,
      viewCount: 10,
    };
  },
  computed: {
    //...mapState('wssMsg',['msg']),
    ...mapState('userInfo',['getUser','isAdmin']),
  },
  methods: {    
    ...mapMutations(['setShowMsg']),
    calculateCols(item) {
      // 모바일 환경에서 모든 그리드 아이템이 전체 너비를 차지하도록 조정
      return this.$vuetify.breakpoint.mdAndUp ? item.w : 12;
    },
    getNewHistoryList() {
      this.loading = true; 
      DynamoDBService.getNewAlertList(this.dataCollectionTime).then((res) => {
        res.forEach((item) => {
          this.items.unshift(item);
          this.dataCollectionTime = item.createdAt;
        })
      })
      .then(() =>{this.getHistoryList();})
      .catch((err) => {
        this.setShowMsg({isVisible:true,color:'error',msg: "getNewHistoryList >>>>>>>>>>" + err});
        this.loading = false;
      });
      setTimeout(() => this.loading = false , 5000);
    },
    // 현재일시 기준 -1일로 from 파라메터 만들어  DynamoDBService.getHistoryList 조회 호출
    getHistoryList() {
      this.loading = true; 
      DynamoDBService.getAlertList(10, 100).then((res) => {
        if(this.items.length === 0){
          this.items = res;
          this.dataCollectionTime = this.items[0].createdAt;
        }else{
          res.forEach((i) => {
            this.items.forEach((j) => {
              if(i.SEQ === j.SEQ){
                j.STATUS = i.STATUS;
                j.BLINK = i.BLINK;
              }
            })
          })
        }
      })
      .catch((err) => {
        this.setShowMsg({isVisible:true,color:'error',msg: "getHistoryList>>" + err});
        this.loading = false;
      });
      setTimeout(() => this.loading = false , 2000);
    },
    computedWidth(p){
      // 여기에서 화면 크기에 맞게 값을 계산하고 반환하세요
      const screenWidth = document.querySelector(".v-main__wrap").clientWidth;
      // console.log("@ computedWidth - screenWidth : ",screenWidth);
      // console.log("@ computedWidth : ",Math.floor(screenWidth * (p*0.01)));
      return Math.floor(screenWidth * (p*0.01)); // 예 50: 화면 너비의 50%를 반환합니다
    },
    computedHeight(p) {
      // 여기에서 화면 크기에 맞게 값을 계산하고 반환하세요
      const screenHeight = document.querySelector(".v-main__wrap").clientHeight;
      // console.log("@ computedHeight - screenHeight : ",screenHeight);
      // console.log("@ computedHeight : ",Math.floor(screenHeight * (p*0.01)));
      return Math.floor(screenHeight * (p*0.01)); // 예 50: 화면 높이의 50%를 반환합니다
    },
    showErrorPopup(errMsg) {
      errMsg = "신림동 지점\n스마일 기사\n010-1111-2222\n\n" + errMsg;
      let param = {isErrorMsg:true,color:'error',msg:errMsg}
      this.setShowMsg(param);
    },
    downloadExcel(val){
      this.loading = true;
      let fileName = '';
      let from, to = '';
      if(val > 1){
        fileName = '3개월 데이터';
        from = moment().subtract(2, "months").format('YYYY-MM-DD');
        to = moment().format('YYYY-MM-DD');
      }else{
        fileName = '분기별 데이터';
        from = moment().subtract(1, "year").format('YYYY-MM-DD');
        to = moment().format('YYYY-MM-DD');
      }
      DynamoDBService.getHistoryList(from, to).then((res) => {
        const workBook = XLSX.utils.book_new();
        const workSheet = XLSX.utils.json_to_sheet(res);
        XLSX.utils.book_append_sheet(workBook, workSheet, fileName);
        XLSX.writeFile(workBook, fileName+'.xlsx');

        this.loading = false;
      })
      .catch((err) => {
        this.setShowMsg({isVisible:true,color:'error',msg: err});
        this.loading = false;
      });
    },
    saveHistory(){
      
      this.loading = true;
      try {
        if (this.selected.length === 0) {

          this.setShowMsg({isVisible:true,color:'error',msg:'수정할 행을 체크하세요'});
          return;
        }
        let list = [];
        this.selected.forEach((item) =>{
          list.push(item);
        });
        DynamoDBService.updateAlertDesc(list).then(() => {
        this.setShowMsg({isVisible:true,color:'primary',msg:'수정 되었습니다.'});
        this.selected = [];
      }).then(() => {this.getHistoryList();});
      } catch (error) {
        this.setShowMsg({isVisible:true,color:'error',msg:"saveHistory >> "+ error});
      }finally{
        this.loading = false;
      }
    },

  },
  mounted() {
    this.getHistoryList();
     this.intvId = setInterval(() => {
       this.getNewHistoryList();
     }, 5000);
  },
  beforeDestroy() {
    clearInterval(this.intvId);
  },
};
</script>
<style scoped>
.vue-grid-item  {
  touch-action: none;
  /* border: 1px solid goldenrod; */
}
.grid-item-background {
  background-color: #1e1e1e;
}

.clock {
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;
  font-weight: 500;  
  text-align: right;  
}

@keyframes blink-effect {
  50% {
    opacity: 0;
  }
}
.blink {
  animation: blink-effect 1s step-end infinite;
}
.fs-10{
  font-size: 1rem !important;
  padding: 10px 0;
}
</style>