<template>
  <v-container fluid >    
    <v-row style="height:500px">
      <v-col
        v-for="item in layout"
        :key="item.i"
        :cols="12"
        :sm="12"
        :md="12"
        class="w-auto h-auto"
        :offset="item.i === 'chart4' ? 6: 0"
      >
        
        <v-card v-if="item.i === 'table3'">
          <v-card-title>
            <v-row dense justify="center">
              <v-col :cols="$vuetify.breakpoint.mobile ? 2 : 2" >
                <v-select
                  :items="floorList"
                  item-text="FLOOR_NM"
                  item-value="FLOOR_ID"
                  label="구역"
                  v-model="selectedFloor"
                  outlined
                  dense
                >
                </v-select>
                
              </v-col>
              <v-col :cols="$vuetify.breakpoint.mobile ? 2 : 2" >
                <v-select
                  :items="floorList"
                  item-text="SECTION_NM"
                  item-value="SECTION_ID"
                  label="섹션"
                  v-model="selectedSection"
                  outlined
                  dense
                ></v-select>
              </v-col>
              <!-- <v-col :cols="$vuetify.breakpoint.mobile ? 2 : 3" >
                <v-text-field
                  :value="selectedFloorInfo ? selectedFloorInfo.data.power : ''"
                  label="해당 층 전력량"
                  outlined
                  readonly
                ></v-text-field>
              </v-col> -->
              <v-col :cols="$vuetify.breakpoint.mobile ? 5 : 3" class="text-right" >
                <span class="clock">최종수신 : {{ dataCollectionTime }}</span>
              </v-col>
              <v-col :cols="$vuetify.breakpoint.mobile ? 5 : 5" class="d-flex justify-end ">
                <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" @click="getHeaterList('C')"><v-icon>mdi-refresh</v-icon> 새로고침</v-btn></div>
                <div v-if="isAdmin" class="ml-2"><v-btn color="orange darken-1" class="subtitle-1" @click="editFloor=true"><v-icon left>mdi-plus</v-icon>구역관리</v-btn></div>
                <div class="ml-2"><v-btn color="error darken-1" class="subtitle-1" @click="deleteHeater"><v-icon left>mdi-minus</v-icon>행삭제</v-btn></div>
                <div class="ml-2"><v-btn color="success darken-1" class="subtitle-1" @click="saveHeaterInfo"><v-icon style="padding-right:5px">mdi-content-save</v-icon> 저장</v-btn></div>
              </v-col>
            </v-row> 
          </v-card-title>    
            <v-data-table
              :headers="headers"
              :items="heaterList"
              :page.sync="page"
              :footer-props="{ disablePagination : false, disableItemsPerPage : true }"
              :height="computedHeight(70)"
              show-select
              v-model="selected"
              item-key="DEVICE_ID"
            >
            
              <template v-slot:[`item.STATUS`]="{ item }">
                <v-chip
                  :color=" item.STATUS == 1 ? 'error' : item.STATUS == 4 ? 'success' : item.STATUS == 5 ? 'warning' : item.STATUS == 0 ? '#333' : '#000' "
                  label
                  small
                >
                  {{ item.STATUS_TEXT }}
                </v-chip>
              </template> 

              <template v-slot:[`item.FLOOR_NM`]="{ item }">
                <v-select
                  :items="floorList"
                  item-text="FLOOR_NM"
                  item-value="FLOOR_ID"
                  v-model="item.FLOOR_ID"
                  outlined
                  dense
                  hide-details
                  :disabled="!isAdmin"
                >
                <option value="">전체</option>
                </v-select>
              </template>

              <template v-slot:[`item.SECTION_NM`]="{ item }">
                <v-select
                  :items="floorList"
                  item-text="SECTION_NM"
                  item-value="SECTION_ID"
                  v-model="item.SECTION_ID"
                  outlined
                  dense
                  hide-details
                  :disabled="!isAdmin"
                >
                <option value="">전체</option>
                </v-select>
              </template>

              <template v-slot:[`item.ON_TEMP`]="{ item }">
                <v-text-field v-model="item.ON_TEMP" @keyup="$isNum2(item,'ON_TEMP')" :disabled="!isAdmin" suffix="˚C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field>                
              </template>
              <template v-slot:[`item.CURR_TEMP`]="{ item }">
                <v-text-field  v-model="item.CURR_TEMP" suffix="˚C"  dark dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field>                
              </template>
              <template v-slot:[`item.OFF_TEMP`]="{ item }">
                <v-text-field  v-model="item.OFF_TEMP" @keyup="$isNum2(item,'OFF_TEMP')" :disabled="!isAdmin" suffix="˚C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right"></v-text-field>                
              </template>
              <template v-slot:[`item.LOCATION`]="{ item }">
                <v-btn color="darken-1" class="subtitle-1" @click="findLocation(item)">위치찾기</v-btn>
              </template>

              <template v-slot:footer>
                <v-container>
                    <v-row dense justify="center">
                      <v-col><v-text-field label="당일 최저온도" v-model="lowest" suffix="˚C" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label="당일 최고온도" v-model="highest" suffix="˚C" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label="당일 평균온도" v-model="average" suffix="˚C" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label="당일 전력량"   v-model="current" suffix="kW" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label="시작온도 일괄입력" v-model="onTemp" @keyup="$isNum1('onTemp')" :disabled="!isAdmin" suffix="˚C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label="종료온도 일괄입력" v-model="offTemp" @keyup="$isNum1('offTemp')" :disabled="!isAdmin" suffix="˚C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>                      
                    </v-row>
                </v-container>
            </template>
            </v-data-table>
          </v-card>        
      </v-col>
    </v-row>
    
    <!-- 구역을 등록 수정하는 팝업  구현하고 팝업창 테두리를 yellow darken-1 -->
    <v-dialog v-model="editFloor" max-width="48dvw" persistent >
      <v-card outlined>
        <v-card-title>
          <v-row class="rounded text-left">
            <v-col :cols="8" >
              <!-- <span class="headline">구역관리</span> -->
              <h3 class="ml-2">구역관리</h3>
            </v-col>
            <v-col :cols="4" class="d-flex justify-end ">                                        
              <v-btn color="secondary" :loading="loading"  @click="editFloor = false">닫기</v-btn>
              <v-btn class="ml-2" color="primary darken-1" :loading="loading"  @click="saveFloor('U')">저장</v-btn>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row dense>
              <v-col cols="2" >
                <v-select
                  :items="['지상','지하']"
                  label="구역종류"
                  v-model="floorType"
                  outlined
                  hide-details
                  dense
                >
                </v-select>
              </v-col>
              <v-col cols="2" >
                <v-text-field label="섹션" readonly required outlined dark dense hide-details width="100" >섹션</v-text-field>
                <!-- <v-select
                  :items="sectionList"
                  item-text="sectionNm"
                  item-value="sectionId"
                  label="섹션 종류"
                  v-model="sectionType"
                  outlined
                  dense
                ></v-select> -->
              </v-col>
              <v-col cols="2" >                
                <v-text-field v-model="floorFrom" @keyup="$isNum1('floorFrom')" label="부터" required outlined dark dense hide-details width="100" ></v-text-field>
              </v-col>
              <v-col cols="2" >                
                <v-text-field v-model="floorTo" @keyup="$isNum1('floorTo')" label="까지" required outlined dark dense hide-details width="100" ></v-text-field>                
              </v-col>
              <!-- <v-col cols="3" >                
                <v-text-field v-model="floorNm" label="별칭" outlined dense hide-details width="100" class="pa-1" ></v-text-field>
              </v-col> -->
              <v-col cols="3" class="mt-1 d-flex justify-end ">
                
                    <v-btn color="orange darken-2" :loading="loading"  @click="createFloor">생성</v-btn>

                    <v-btn class="ml-2" color="error" :loading="loading" @click="saveFloor('D')">삭제</v-btn>
                  
              </v-col>
            </v-row>
            <v-row dense>
              <v-col cols="12" >
                <!-- 데이터테이블로 등록수정 -->
                <v-data-table
                  dense
                  divider
                  fixed-header
                  show-select
                  v-model="floorChkList"
                  :headers="floorHeaders"
                  :items="floorList"
                  item-key="FULL_ID"
                  :items-per-page="10"
                  :footer-props="{ itemsPerPageOptions: [10,20,30,50,'All'] }"
                  >
                    <!-- <template v-slot:[`item.chk`]="{ item }">
                      <input type="checkbox" class="mt-2" style="transform: scale(1.5)" v-model="item.chk" >
                    </template> 
                    <template v-slot:[`item.floorType`]="{ item }">
                      <v-select
                        :items="floorList"
                        item-text="floorNm"
                        item-value="floorId"
                        v-model="item.floorId"
                        outlined
                        dense
                        hide-details
                      ></v-select>
                    </template>-->
                    <template v-slot:[`item.FLOOR_NM`]="{ item }">
                      <v-text-field v-model="item.FLOOR_NM" outlined dark dense small hide-details width="50%" class="pa-1" ></v-text-field>
                    </template>
                    <template v-slot:[`item.SECTION_NM`]="{ item }">
                      <v-text-field v-model="item.SECTION_NM" outlined dark dense small hide-details width="50%" class="pa-1" ></v-text-field>
                    </template>
                    
                  </v-data-table>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="80"
        width="10"
        color="primary lighten-2"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapState , mapMutations} from "vuex";
import moment from 'moment';

export default {  
  data() {
    return {
      loading: false,
      isDraggable: false,
      editFloor:false,
      editHeater:false,
      selected: [],
      floorHeaders: [        
        { text: "구역명", value: "FLOOR_NM" ,width:"50%"},
        { text: "섹션명", value: "SECTION_NM" ,width:"50%"}
      ],
      floorList: [],
      floorChkList: [],
      floorType:"지하",
      floorNm:"",
      sectionNm:"",
      floorFrom:1,
      floorTo:5,
      floorId: "",
      sectionId: "",
      selectedFloor: null, // 선택된 층의 floorId
      selectedSection: null, //선택된 섹션의 sectionId
      heaterList: [],
      dataCollectionTime: '00:00:00',
      layout: [
        { x: 0, y: 0, w: 12, h: 8, i: "table3" },
        { x: 0, y: 4, w: 12, h: 8, i: "chart1" },
      ],      
      onTemp : null,
      offTemp : null,
      lowest: 0,
      highest: 0,
      average: 0,
      current: 0,
      headers: [
        //{ text: "No.", value: "no", align: 'center' , sortable: false, width:"70" },         
        { text: "일련번호", value: "DEVICE_ID",  width:"120"},
        { text: "구역", value: "FLOOR_NM", width:"15%"},
        { text: "섹션", value: "SECTION_NM", width:"15%"},
        { text: "시작온도", value: "ON_TEMP" , width:"120" },
        { text: "현재온도", value: "CURR_TEMP" , width:"100"},
        { text: "종료온도", value: "OFF_TEMP" , width:"120"},
        { text: "상태", value: "STATUS" , width:"80"},  
        { text: "전력량", value: "CURRENT" , width:"80"},
        { text: "", value: "LOCATION" , width:"80"}
      ],
      //lastRcvTime: [],
      pagination: {rowsPerPage: 10},
      page: 1,
      newItems: [],
    };
  },
  computed: {
    //...mapState('wssMsg',['msg','isConn']),
    ...mapState('userInfo',['getUser','isAdmin']),
    

  },
  watch: {
  //  msg(newVal){
  //     if (newVal) {
  //       const timestamp = new Date().toLocaleString();
  //       this.dataCollectionTime = timestamp;
        
  //       // lastRcvTime의 terminalId 비교후  현재시간(시분초)를 추가
  //       const newData = JSON.parse(newVal) || {};
  //       this.lastRcvTime.forEach((item) => {
  //         if( newData.terminalId === item.terminalId ){         
  //           item.rcvTime = moment().format('HHmmss');
  //           console.log("\n-- lastRcvTime updated!! : ",newData.terminalId,item.terminalId)
  //         }
  //       });

  //       // lastRcvTime 에서 newVal.terminalId가 없으면 추가
    //       let found = this.lastRcvTime.find(item => item.terminalId === newData.terminalId);
    //       if( !found ){
    //         console.log("\n-- lastRcvTime added!! : ",newData.terminalId,found)
    //         this.lastRcvTime.push({terminalId:newData.terminalId,rcvTime:moment().format('HHmmss')});
    //       }

  //       this.updateHeaterInfo(newVal);
  //     }
  //   },
    
    
  //   isConn(stat){      
  //       // stat 에 따라 heaterList 의 connect 값을 모두 'Y' 혹은 'N' 으로 변경
  //       console.log("\n--- HeaterManage isConn",stat)
  //       this.heaterList.forEach((heater) => {
  //         if( !stat ){
  //           heater.connect = 'N';
  //           heater.msg = `[히터응답오류]-${heater.DEVICE_ID}`
  //           // History 테이블에 저장
  //           this.saveHistory(heater);

  //         }
  //       });              
  //   },
   
    selectedFloor(newVal) {
      console.log(newVal);
      this.floorId = newVal;
      this.getHeaterList(); 
    },

    editFloor(newVal){
      if( !newVal ){
        this.getFloorList();
        this.getHeaterList();
      }
      this.floorList = this.floorList.filter((item) => item.FLOOR_ID !== "");
    },
    editHeater(){
      
      alert(this.heaterChecked);
      //console.log(this.heaterList[idx]);
    },

    selectedSection(newVal) {
           
      this.sectionId = newVal;
      this.getHeaterList(); 
    },

  },

  
  methods: {    
    ...mapMutations(['setShowMsg']),
    isNumber(evt) {
      console.log("-- isNumber",evt)

      if( isNaN(evt) ) {
        // evt 값을 정규식으로 숫자만 남기고 제거
         evt = evt.replace(/[^0-9]/g,'');
      }

      console.log("-- replace ",evt)

    },
    findLocation(item){
      this.loading = true;
      console.log(item.DEVICE_ID);
      DynamoDBService.updatecommand(item.DEVICE_ID);
      setTimeout(() => this.loading = false , 1000);
      
    },
    calculateCols(item) {
      // 모바일 환경에서 모든 그리드 아이템이 전체 너비를 차지하도록 조정
      return this.$vuetify.breakpoint.mdAndUp ? item.w : 12;
    },
    getFloorList() {
      this.loading = true;
      
      DynamoDBService.getFloorList().then((res) => {
        this.floorList = res;
        let arr = {
          "FLOOR_ID": "",
          "FLOOR_NM": "전체",
          "SECTION_ID":"",
          "SECTION_NM":"전체"
        };
        this.floorList.unshift(arr);
      });
      this.loading = false;
    },
    getNewHeaterList(){
      let param = {
          "FLOOR_ID": this.floorId,
          "SECTION_ID": this.sectionId,
          "LAST_DATE": this.dataCollectionTime
      };
      this.loading = true;
      DynamoDBService.getNewHeaterList(param).then((res) => {
        res.forEach((item)=>{
          this.heaterList.unshift(item);
        })
        this.loading = false;
      }).then(() =>{this.getHeaterList();});
    },
    getHeaterList(cmd) {
      let param = {
          "FLOOR_ID": this.floorId,
          "SECTION_ID": this.sectionId,
      };
      this.loading = true;
      // 선택된 층의 히터 정보를 찾아 heaterList에 저장
      //최종 현재 시간을 기준으로 데이터를 조회해서 
      // 데이터가 있으면 this.heaterList.push()
      console.log(cmd);
      DynamoDBService.getHeaterList(param).then((res) => {
        if('C' != cmd && this.heaterList.length !== 0){
          for(var i=0; i<this.heaterList.length; i++){
            for(var j=0; j<res.length; j++){
              const item = res[j];
              if(this.heaterList[i].DEVICE_ID == item.DEVICE_ID){
                this.heaterList[i].CURR_TEMP = item.CURR_TEMP;
                this.heaterList[i].STATUS = item.STATUS;
                this.heaterList[i].STATUS_TEXT = item.STATUS_TEXT;
                this.heaterList[i].CURRENT = item.CURRENT;
                
               // const idx = res.findIndex(function(i) {return i.DEVICE_ID === item.DEVICE_ID}) // findIndex = find + indexOf
               // if (idx > -1) res.splice(idx, 1)
                break;
              }
            }
          }
        }else{
          this.heaterList = res;
          this.onTemp = null;
          this.offTemp = null;
        }
        this.loading = false;
      });
    },
    getRandomNumber(numbers) {
        // let numbers = [26, 27, 28];
        let randomIndex = Math.floor(Math.random() * numbers.length);
        return numbers[randomIndex];
    },
    /*
    updateHeaterInfo() {
      const newData = JSON.parse(this.msg[0]) || {};
      this.totKw = 0;
      this.totDeg = 0;

      // socket.io 수신데이터 newData 를 parsing 하여 currTemp 값을 heaterList 의 currTem 에 반영
      this.heaterList.forEach((heater) => {
        // console.log("-- heater.deviceId",heater.deviceId)
        // console.log("-- newData.deviceId",newData.deviceId)
        
        //const found = newData[heater.deviceId];
        //console.log("-- found",found)
        if (heater.terminalId === newData.terminalId) {
          //heater.currTemp = this.getRandomNumber([26, 27, 28, 27, 27, 27, 27]); // newData.currTemp;
          //heater.power = this.getRandomNumber([2.1,2.8,1.8,2.3,2.3,2.3,2.3]); // newData.power;
          heater.currTemp = newData.currTemp;
          heater.power = newData.power;
          heater.msg = newData.msg;

          heater.connect = 'Y';
        }

        this.totDeg += heater.currTemp;
        this.totKw += (heater.power || 0 );
      });

      // this.totDeg를 this.totDeg의 평균값으로 변경 ( 단, 정수로 반올림 )
      if( this.heaterList.length ) this.totDeg = Math.round(this.totDeg / this.heaterList.length);
      // this.totKw 는 Ampare 합계이므로  kW로 변환하고  소수점 1자리까지 반올림하여 표시
      this.totKw = Math.round(this.totKw * 220 / 1000 * 10) / 10;
      
      
    },
    */
    computedWidth(p){
      // 여기에서 화면 크기에 맞게 값을 계산하고 반환하세요
      const screenWidth = document.querySelector(".v-main__wrap").clientWidth;
      // console.log("@ computedWidth - screenWidth : ",screenWidth);
      // console.log("@ computedWidth : ",Math.floor(screenWidth * (p*0.01)));
      return Math.floor(screenWidth * (p*0.01)); // 예 50: 화면 너비의 50%를 반환합니다
    },
    computedHeight(p) {
      // 여기에서 화면 크기에 맞게 값을 계산하고 반환하세요
      const screenHeight = document.querySelector(".v-main__wrap").clientHeight;
      // console.log("@ computedHeight - screenHeight : ",screenHeight);
      // console.log("@ computedHeight : ",Math.floor(screenHeight * (p*0.01)));
      return Math.floor(screenHeight * (p*0.01)); // 예 50: 화면 높이의 50%를 반환합니다
    },
    saveHeaterInfo() {
      // 히터정보 저장전 heaterList.data.onTemp ,heaterList.data.offTemp 값을 totOnTemp, totOffTemp 로 저장
      console.log(this.onTemp);
      console.log(this.offTemp);
      this.heaterList.forEach((heater) => {
        heater.ON_TEMP = this.onTemp || heater.ON_TEMP ;
        heater.OFF_TEMP = this.offTemp || heater.OFF_TEMP;
      });
      
      // 히터 정보 저장
      DynamoDBService.saveHeaterList(this.heaterList).then((res) => {
        console.log("히터 정보 저장 결과 : ",res);
        this.setShowMsg({isVisible:true,color:'primary',msg:'저장 되었습니다.'});
//        this.getFloorList();
        this.getHeaterList('C');
      });

      this.onTemp = null;
      this.offTemp = null;
    },   
    deleteHeater(){
      this.loading = true;
      // 체크 없으면 삭제할 구역이 없습니다. 메시지
        if (this.selected.length === 0) {
          this.setShowMsg({isVisible:true,color:'error',msg:'삭제할 행을 체크하세요'});
          return;
        }
        let delHeaterList = '';

        this.selected.forEach((item) =>{
          delHeaterList += '\'' + item.DEVICE_ID + '\',';
        });
        console.log("삭제될 디바이스" + delHeaterList);
        delHeaterList = delHeaterList.slice(0, -1);
        DynamoDBService.deleteHeater(delHeaterList).then(() => {
          this.getHeaterList('C'); 
      })
      .finally(() => { 
        this.loading = false; 
        this.setShowMsg({isVisible:true,color:'primary',msg:'삭제 되었습니다.'});
      });
    },
    createFloor() {
      let floorTypeCd = "B"
      let floorNm = this.floorNm;
      let floorFrom = this.floorFrom || 0;
      let floorTo = this.floorTo || 0;

      // from ,to 가 0 이하 이거나 null 이면 메시지
      if (floorFrom <= 0 || floorTo <= 0) {
        this.setShowMsg({isVisible:true,color:'error',msg:'시작층과 종료층은 0보다 커야합니다.'});
        return;
      }
      //from 보다 to 가 작으면 메시지
      if (floorFrom > floorTo) {
        this.setShowMsg({isVisible:true,color:'error',msg:'시작층이 종료층보다 큽니다.'});
        return;
      }

      floorTypeCd = this.floorType === "지하" ? "B" : "F";

      // floorFrom 부터 floorTo 까지 floorList에 제일 첫번째 번지로 내림차순으로 추가
      let dupCnt = 0;
      for (let i = floorTo; i >= floorFrom; i--) {
        for(let j=floorTo; j >= floorFrom; j--){

          // floorList에 floorId가 이미 존재하면 continue
          if (this.floorList.find((item) =>  item.FLOOR_ID+item.SECTION_ID === `${floorTypeCd}${i}+S${j}`) ) {
            dupCnt++;
            continue;
          }

          this.floorList.unshift({
            FLOOR_ID: `${floorTypeCd}${i}`, 
            SECTION_ID: `S${j}`, 
            FLOOR_NM: `${this.floorType}${i}층 ${floorNm}`,
            SECTION_NM: `섹션${j}`
          });
        }
      }

      let param = "";
      if (dupCnt > 0) { 
        // confirm 메시지
        param = {isVisible:true,color:'warning',msg:`${floorFrom} ~ ${floorTo} 중 ${dupCnt}개 구역이 이미 존재합니다.\n저장하시겠습니까?`}
      } else {
        // confirm 메시지 총 n개 구역을 저장하시겠습니까?
        param = {isVisible:true,color:'warning',msg:`총 ${floorTo - floorFrom + 1} 개 구역을 생성 하시겠습니까?`}
      }

      // 브라우저 기본 컨펌창
      if (confirm(param.msg)) {
       // this.saveFloor('U');
      }
    },
    saveFloor(cmd) {

      let paramList = [];
  
      // cmd 가 D 이면 삭제하시겠습니까? confirm 메시지
      if (cmd === 'D') {
        
        // 체크 없으면 삭제할 구역이 없습니다. 메시지
        if (this.floorChkList.length === 0) {
          this.setShowMsg({isVisible:true,color:'error',msg:'삭제할 구역을 체크하세요'});
          return;
        }

        let param = {isVisible:true,color:'warning',msg:`선택된 구역을 삭제하시겠습니까?`}

        // 히터가 등록된 구역을 삭제시 "등록된 히터가 존재하는 구역입니다 삭제하시겠습니까?" 컨펌후 삭제시 해당 히터기기의 구역을 "구역대기" 로 자동 변경 되도록
        let found = this.heaterList.find(item => this.floorChkList.find(chk => chk.FLOOR_ID+chk.SECTION_ID === item.FLOOR_ID+item.SECTION_ID));
        let heaterCnt = 0;
        if( found ){
          param = {isVisible:true,color:'warning',msg:`등록된 히터가 존재하는 구역입니다.\n삭제하시겠습니까?(삭제후 구역대기로 이동됨)`}
          heaterCnt = 1;
        }

        // 브라우저 기본 컨펌창
        if (!confirm(param.msg)) {
          return;
        }
        // floorChkList 에서 selectedFloor 만 추출하여 floorList 에 저장
        paramList = this.floorChkList.map((item) => {
          console.log(item);
          return {
            FLOOR_ID: item.FLOOR_ID,
            SECTION_ID: item.SECTION_ID,
            FLOOR_NM: item.FLOOR_NM,
            SECTION_NM: item.SECTION_NM,
            heaterCnt: heaterCnt
          };
        });
      }else{
        paramList = this.floorList;
        // for(var i=0; i<this.floorList.length; i++){
        //   paramList.push(
        //       {
        //         FLOOR_ID: this.floorList.FLOOR_ID, 
        //         SECTION_ID: this.floorList.SECTION_ID,
        //         FLOOR_NM: this.floorList.FLOOR_NM, 
        //         SECTION_NM: this.floorList.SECTION_NM
        //       }
        //     )
        // }
      }

      this.loading = true;

      if( paramList.length === 0 ){
        this.setShowMsg({isVisible:true,color:'error',msg:'구역정보가 없습니다.'});
        this.loading = false;
        return;
      }
       
      //구역정보 저장
      DynamoDBService.saveFloorList(paramList,cmd).then(() => {
        this.setShowMsg({isVisible:true,color:'primary',msg:'구역정보가 저장되었습니다.'});
        //this.getFloorList();
         this.editFloor = false;
      })
      .catch((err) => {
        console.log(err);
        this.setShowMsg({isVisible:true,color:'error',msg:'구역저장에 실패하였습니다.'});        
      })
      .finally(() => {
        this.loading = false;
        this.floorList = [];
      });
      
    },
    showErrorPopup(errMsg) {
      errMsg = "신림동 지점\n스마일 기사\n010-1111-2222\n\n" + errMsg;
      let param = {isErrorMsg:true,color:'error',msg:errMsg}
      this.setShowMsg(param);
    },
    saveHistory(heater) {
      // History 테이블에 저장시 id 필드 제거
      // delete heater.id;
      DynamoDBService.saveHistory(heater).then((res) => {
        console.log("\n--- History 저장 : ",res);
      })
      .catch((err) => {
        console.log("\n--- History 저장 오류! " ,err);
      })
    },

    //최신데이터 조회 평균온도값, 총전력값, 최종수신값
    getNewDate(){
      DynamoDBService.getNewHistory().then((res) => {
        this.lowest = res[0].lowest;
        this.highest = res[0].highest;
        this.average = res[0].highest;
        this.current = res[0].CURRENT;
        this.dataCollectionTime = res[0].createdAt
      });
    }
  },
  mounted() {
    // Floor List
    this.getNewDate();
    this.getFloorList();
    this.getHeaterList();
    


    // heaterList 가 존재하면 5초마다 lastRcvTime 의 terminalId 와 비교하여 1분이상 수신이 없으면 connect 값을 'N' 으로 변경
    this.intvId = setInterval(() => {
      this.getNewDate();
      this.getNewHeaterList();
      
      if( this.heaterList.length ){
        this.heaterList.forEach((heater) => {
          let currTime = moment().format('HHmmss');
          let arr = (heater.updatedAt).split(' ');
          let rcvTime = arr[1].replaceAll(':', ' ');

          let diff = currTime - rcvTime;
          if(diff > (60*10)){
            heater.STATUS = 5;
            this.saveHistory(heater);
            this.saveHeaterList(heater);
          }

          
          // const found = this.lastRcvTime.find(item => item.DEVICE_ID === heater.DEVICE_ID);
          // // console.log("-- 1분 라이브체크 heater.terminalId : ",heater.terminalId)
          // if( found ){
          //   let rcvTime = fou-nd.rcvTime;
          //   let currTime = moment().format('HHmmss');

          //   let diff = currTime - rcvTime;            
          //   console.log("-- diff",diff)
          //   if( diff > 60 ){
          //     heater.connect = 'N';
          //     heater.msg = `[${Math.floor(diff/60)}분간 응답없음]`

          //     // History 에 저장
          //     this.saveHistory(heater);

          //     // history 에 저장후 lastRcvTime 에서 삭제
          //     const idx = this.lastRcvTime.findIndex(item => item.DEVICE_ID === heater.DEVICE_ID);
          //     if( idx > -1 ){
          //       this.lastRcvTime.splice(idx,1);
          //     }

          //   }
          // }
        });
      }
    }, 5000);

  },
  beforeDestroy() {
    clearInterval(this.intvId);
  },
};
</script>
