import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css';
import VueRouter from 'vue-router';
import VueApexCharts from 'vue-apexcharts';
import { GridLayout, GridItem } from 'vue-grid-layout';
import VueDraggableResizable from "vue-draggable-resizable";
import 'vue-draggable-resizable/dist/VueDraggableResizable.css'

Vue.config.productionTip = false;

Vue.use(Vuetify);
Vue.use(VueRouter);
Vue.use(VueApexCharts);
Vue.use(GridLayout);
Vue.use(GridItem);
Vue.use(VueDraggableResizable);



Vue.component('apexchart', VueApexCharts);
Vue.component('grid-layout', GridLayout);
Vue.component('grid-item', GridItem);
Vue.component('vue-draggable-resizable', VueDraggableResizable);

Vue.prototype.$isNum2 = function (item, key) {
  if(isNaN(item[key])) {
    item[key] = item[key].replace(/[^0-9]/g,'');
  }
}

Vue.prototype.$isNum1 = function (field) {
  if(isNaN(this[field])) {
    this[field] = this[field].replace(/[^0-9]/g,'');
  }
}

// 영문,숫자만 입력
Vue.prototype.$isEngNum = function (item, key) {
  if(key){    
    if(isNaN(item[key])) {
      item[key] = item[key].replace(/[^a-zA-Z0-9]/g,'');
    }
  }else{
    if(/[^a-zA-Z0-9]/.test(this[item])) {
      this[item] = this[item].replace(/[^a-zA-Z0-9]/g,'');
    }
  }
}

// 한글만 입력
Vue.prototype.$isKor = function (field) {
  if(/[^ㄱ-ㅎㅏ-ㅣ가-힣]/.test(this[field])) {
    this[field] = this[field].replace(/[^ㄱ-ㅎㅏ-ㅣ가-힣]/g,'');
  }
}

const vuetify = new Vuetify({
  // 여기에 옵션을 추가할 수 있습니다.
});

new Vue({
  router,
  store,
  vuetify, // vuetify 객체를 Vue 인스턴스에 등록합니다.
  
  render: (h) => h(App),
}).$mount('#app');
