import DynamoDBService from '@/services/DynamoDBService.js';

export default {
    namespaced: true,
    state: {        
        user:{role:'USER'},
        isAdmin:false,
    },
    getters: {
        getRole: (state) => state.user.role,
        getUser: (state) => state.user,
        isAdmin: (state) => state.isAdmin,
    },
    mutations: {        
        setUser(state, payload) {
            state.user = payload;
            state.isAdmin = (payload.role === 'ADMIN') ? true : false;
        },
    },
    actions: {        
        async fetchUser({ commit } , user) {
            try {
                const response = await DynamoDBService.getUser(user);
                console.log(`login SUCC : ${response}`)
                response ? commit('setUser', response) : null;
                return response;
            } catch (error) {
                console.error('Store Failed to fetchUser() :', error);                
                throw error;
            }
        },
    },
};