// src/store/theme.js
export default {
    state: {
      darkMode: true,
    },
    mutations: {
      toggleDarkMode(state) {
        state.darkMode = !state.darkMode;
      },
      setDarkMode(state) {
        state.darkMode = true;
      },
    },
    getters: {
      darkMode: (state) => state.darkMode,
    },
  };
  