<template>
  <v-container fluid>
    <v-card outlined class="mt-5">
      <v-card-title>        
        <v-row class="secondary darken-1 rounded text-left">
          <v-col :cols="6" >
            <h3 class="ml-2">도면 자료관리</h3>
          </v-col>
          <v-col :cols="6" class="d-flex justify-end ">            
            <div class="ml-2"><v-btn  color="primary darken-1" class="subtitle-1" :loading="loading" @click="getFileList">🔄 새로고침</v-btn></div>
            <div class="ml-2" v-if="isAdmin"><v-btn  color="primary darken-1" class="subtitle-1" :loading="loading" @click="editRow({})">➕ 등록</v-btn></div>
          </v-col>
        </v-row>
      </v-card-title>
      <v-data-table 
        class="my-data-table"       
        divider
        fixed-header
        :headers="headersFileList"
        :items="tableItemsFile"        
        :items-per-page="10"
        :footer-props="{ itemsPerPageOptions: [10,20,30,50,'All'] }"
        :hide-default-footer="false"
      >
        <template v-slot:[`item.no`]="{ index }">
          <span>{{ index + 1 }}</span>
        </template>        
        <template v-slot:[`item.attchFiles`]="{ item }">
          <!-- JSON attchFiles 요소 리스트 표시 하고 각 리스트 마다 삭제버튼 표시 -->
          <v-row dense v-for="(file,index) in item.attchFiles" :key="index"  class="d-flex justify-center">
            <v-col cols="12" class="d-flex justify-start">
              <v-chip class="ma-1" color="green darken-3" text-color="white" label >
                <v-icon left @click="downloadFile(file)" >{{ getFileIcon(file.fileExt) }}</v-icon>
                <span class="truncate" @click="downloadFile(file)">{{ file.fileName }} ({{file.fileSize}})</span>
              </v-chip>              
            </v-col>
          </v-row>

        </template>        
        <template v-if="isAdmin" v-slot:[`item.edit`]="{ item }">
          <v-btn color="primary darken-1" dense small  @click="editRow(item)">수정</v-btn>
          <v-btn color="error" dense small class="ml-1" @click="delRow({groupId:item.groupId})">삭제</v-btn>
        </template>
      </v-data-table>
    </v-card>
    <!-- 업로드파일 수정 팝업 -->
    <v-dialog v-model="isEdit" max-width="50dvw" persistent>
      <v-card color="secondary darken-1" outlined>
        <v-card-title>
          <span class="headline">등록/수정</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field v-model="editedItem.title" label="제목"
                 clear-icon="mdi-close-circle"
                 clearable 
                 @click:clear="editedItem.title = ''"
                 required></v-text-field>
              </v-col>
            </v-row>
            <!-- JSON attchFiles 요소 리스트 표시 하고 각 리스트 마다 삭제버튼 표시 -->
            <v-row dense v-for="(file,index) in editedItem.attchFiles" :key="index"  class="d-flex justify-center">
              <v-col cols="12" class="d-flex justify-start">
                <v-chip class="ma-1" color="green darken-3" text-color="white" label >                  
                  <v-icon left @click="downloadFile(file)" >{{ getFileIcon(file.fileExt) }}</v-icon>
                  <span @click="downloadFile(file)">{{ file.fileName }} ({{file.fileSize}})</span>
                  <v-icon color="error darken-2" right @click="delRow({groupId:editedItem.groupId,seq:file.seq})">mdi-close</v-icon>
                </v-chip>              
              </v-col>
            </v-row>
            <v-row dense>
              <v-col cols="12">
                <!-- muliti file upload 컴포넌트 구현 -->
                <v-file-input
                  v-model="files"                  
                  accept="*/*"
                  label="첨부파일"
                  outlined
                  dense
                  multiple
                  required
                  prepend-icon="mdi-paperclip"
                  @change="onFileChange"
                ></v-file-input>                
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" outlined  @click="isEdit = false">취소</v-btn>
          <v-btn color="primary lighten-2" outlined  @click="save(editedItem)">저장</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="80"
        width="10"
        color="primary lighten-2"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapGetters ,mapMutations} from 'vuex';
import moment from 'moment';

export default {
  data() {
    return {
      loading: false,
      div:'PRINT', // 도면
      headersFileList: [
        { text: "No.", value: "no" , align: "center",width:"50"},
        { text: "제목", value: "title", widht:"35%"},
        { text: "첨부파일", value: "attchFiles" , sortable: false , width:"15%"},
        { text: "다운로드", value: "downloadCnt" , align: "center",width:"100"},
        { text: "작성자", value: "creater",width:"150" },
        { text: "수정일시", value: "updatedAt",width:"150" },
        { text: "수정", value: "edit" ,width:180 , align: "center"},
      ],
      tableItemsFile: [],      
      editedItem:{},
      files: [],
      uploadedImage: null,
      isEdit : false,
    };
  },
  computed: {
    moment() {
      return moment;
    },
    ...mapGetters('userInfo',['getUser','isAdmin']),
    getFileIcon() {        
        return (ext) => {
            switch(ext.toLowerCase()) {
                case '.pdf':
                    return 'mdi-file-pdf'
                case '.doc':
                case '.docx':
                    return 'mdi-file-word'
                case '.xls':
                case '.xlsx':
                    return 'mdi-file-excel'
                case '.ppt':
                case '.pptx':
                    return 'mdi-file-powerpoint'
                case '.jpg':
                case '.jpeg':
                case '.png':
                case '.gif':
                    return 'mdi-file-image'
                case '.txt':
                    return 'mdi-file-document-outline'
                case '.mp4':
                case '.avi':
                    return 'mdi-file-video'
                case '.zip':
                    return 'mdi-zip-box'  // zip 파일 아이콘을 반환합니다.
                case '.exe':
                    return 'mdi-application'  // exe 파일 아이콘을 반환합니다.
                default:
                    return 'mdi-file'
            }
        }
    }
  },
  methods: {
    ...mapMutations(['setShowMsg']),
    onFileChange(e) {
      console.log("onFileChange=>",e.target)
    },
    editRow(editItem){
      console.log("edit=>",editItem)
      this.isEdit = true;
      this.files = [];
      this.editedItem = Object.assign({},editItem || {});

    },
    async delRow(item){
      console.log("delRow=>",item)
      // this.tableItemsUser.splice(index,1);
      
      if( confirm("삭제하시겠습니까?") ){
        this.loading = true;
        const param = item.seq ? { groupId: item.groupId, seq: item.seq } : { groupId: item.groupId } 
        await DynamoDBService.delFile(param)
        .then((data)=>{
          console.log("--- delRow OK!",data);
          this.setShowMsg({isVisible:true,color:'success darken-1',msg:'삭제 되었습니다.'})
          if(item.seq){
            // editedItem.attchFiles 의 해당 seq 삭제
            this.editedItem.attchFiles = this.editedItem.attchFiles.filter((file)=>file.seq != item.seq);

          } else{            
            setTimeout(()=>{          this.getFileList();        },500)
          }

        })
        .catch((err)=>{
          console.error(err)
          this.setShowMsg({isErrorMsg:true,color:'error',msg:err.message});
        }).finally(()=>{
          this.loading = false;
        })
      }
    },
    async save(editItem){
      console.log("save=>",editItem)
      const formData = new FormData();
      
      editItem.groupId ? formData.append('groupId', editItem.groupId) : null;
      formData.append('div', this.div);
      for (let i = 0; i < this.files.length; i++) {
        formData.append('files', this.files[i]);
      }
      formData.append('title', editItem.title);
      formData.append('creater', this.getUser.usrNm);

      if(!editItem.title){
        this.setShowMsg({isVisible:true,color:'error',msg:'제목을 입력하세요.'})
        return;
      }
      if( !editItem.groupId && (!this.files || this.files.length == 0)){
        this.setShowMsg({isVisible:true,color:'error',msg:'첨부파일을 선택하세요.'})
        return;
      }

      this.loading = true;

      // 파일 업로드 DynamoDBService saveFile(file) 호출
      await DynamoDBService.saveFile(formData)
      .then((data)=>{
        console.log("--- saveFile OK!",data);
        this.setShowMsg({isVisible:true,color:'success darken-1',msg:'저장 되었습니다.'})
        this.isEdit = false;
        setTimeout(()=>{          this.getFileList();        },500)
      })
      .catch((err)=>{
        console.error(err)
        this.setShowMsg({isErrorMsg:true,color:'error',msg:err.message});
      }).finally(()=>{
        this.loading = false;
      })
      
    },
    async downloadFile(item){
      console.log("downloadFile=>",item)
      this.loading = true;
      await DynamoDBService.downloadFile(item)
      .then((data)=>{
        console.log("--- downloadFile OK!",data);
        this.setShowMsg({isVisible:true,color:'success darken-1',msg:'다운로드 되었습니다.'})
        this.isEdit = false;
        setTimeout(()=>{          this.getFileList();        },500)
      })
      .catch((err)=>{
        console.error(err)
        this.setShowMsg({isErrorMsg:true,color:'error',msg:err.message});
      }).finally(()=>{
        this.loading = false;
      })
    },
    async getFileList(){      
      this.loading = true;
      this.tableItemsFile = [];
      await DynamoDBService.getFileList(this.div)
      .then((data)=>{
        console.log("--- getPrintFileList OK!",data);
        this.tableItemsFile = data;
      })
      .catch((err)=>{
        console.error(err)
        this.setShowMsg({isErrorMsg:true,color:'error',msg:err.message});
      })
      .finally(()=>{
        this.loading = false;
        this.editedItem = {};
      })
    },
  },
  mounted() {
    console.log("mounted.")
    let mom = moment();console.log(mom);
    this.getFileList();

    
  },
};
</script>

<style scoped>

.layout-card {
  margin: 10px 0;
  border-width: 2px !important; /* 두께 변경 */
  border-color: #aaa !important; /* 색상 변경 */
}

.mb-5 {
  margin-bottom: 2rem;
}

/* .compobox {
    border-width: 2px;
    border-color: #aaa !important; 
    color: #aaaa;
    height: 100px;
  } 
  */

  .layout-table {
  width: 100%;
  border: 2px solid #aaa; /* border color and thickness */
  margin: 2px 0;
}

::v-deep .v-data-table__wrapper thead tr th {
  font-size: 16px !important; /* 원하는 크기로 변경하세요 */
}

::v-deep .v-data-table__wrapper tbody tr td {
  font-size: 14px !important;
}

.layout-table td {
  border: 2px solid #aaa; /* cell border color and thickness */
  text-align: center;
  padding: 5px;
}

.compobox {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.truncate {
  display: inline-block;
  max-width: 25ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

</style>
