<template>
  <v-container fluid>    
    <v-card outlined class="mt-5">
      <v-card-title>        
        <v-row class="secondary darken-1 rounded text-left">
          <v-col :cols="6">
            <h3 class="ml-2">사용자 관리</h3>
          </v-col>
          <v-col :cols="6" class="d-flex justify-end ">
            <div><v-btn color="primary darken-1" class="subtitle-1" :loading="loading" @click="addUser" >➕ 추가</v-btn></div>
            <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" :loading="loading" @click="getUserList">🔄 새로고침</v-btn></div>
            <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" :loading="loading" @click="saveUserList">💾 저장</v-btn></div>
          </v-col>
        </v-row>
      </v-card-title>
      <v-data-table        
        divider
        fixed-header
        :headers="headersUser"
        :items="tableItemsUser"                
        :hide-default-footer="true"
      >
        <template v-slot:[`item.usrId`]="{ item }">
          <v-text-field
            :disabled="item.createdAt ? true : false"
            v-model="item.usrId"
            @keyup="$isEngNum(item,'usrId')"
            :outlined="!item.createdAt"
            :rules="[v => !!v || '아이디는 필수입니다.']"
            dark
            dense
            hide-details
            width="100"
            class="pa-1">
          </v-text-field>
        </template>
        <template v-slot:[`item.usrNm`]="{ item }">
          <v-text-field v-model="item.usrNm" outlined dark dense hide-details width="100" class="pa-1" ></v-text-field>
        </template>
        <template v-slot:[`item.pwd`]="{ item }">
          <v-text-field v-model="item.pwd" outlined dark dense hide-details width="100" class="pa-1" ></v-text-field>
        </template>
        <template v-slot:[`item.role`]="{ item }">
          <v-select
          :items="['ADMIN','USER']"
          v-model="item.role"
          outlined
          dense
          hide-details
          class="select-darkgoldenrod"
          @input="editItem(item)"
          ></v-select>
        </template>
        <template v-slot:[`item.isLock`]="{ item }">
          <v-select
          :items="['Y','N']"
          v-model="item.isLock"
          outlined
          dense
          hide-details
          class="select-darkgoldenrod"
          ></v-select>
        </template>
        <template v-slot:[`item.delRow`]="{ item , index }">
          <v-btn color="error" :loading="loading" @click="delRow(item,index)">삭제</v-btn>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapMutations } from 'vuex';
import moment from 'moment';

export default {
  data() {
    return {
      loading: false,
      alarm:true,
      groupNumbers: [0,100,295],
      sensorNumbers: [],
      selectedGroup: null,
      selectedSensor: null,
      headersSensor: [
        { text: "센서노드", value: "node" },
        { text: "Index", value: "indexNm" },
        { text: "Battery", value: "battery" },
        { text: "최근 측정", value: "rcvDtm" },
        { text: "1차관리기준", value: "base1" },  
        { text: "2차관리기준", value: "base2" },  
        { text: "3차관리기준", value: "base3" },  
        { text: "Factor", value: "factor" },
        { text: "Offset", value: "offset" },
        { text: "Zero Set", value: "zeroSet" },
        /* 
        { text: "X-팩터", value: "xFactor" },
        { text: "Y-팩터", value: "yFactor" },
        { text: "X-오프셑", value: "xOffset" },
        { text: "Y-오프셑", value: "yOffset" }, 
        */
      ],
      tableItemsSensor: [],      
      headersGw: [
        { text: "게이트웨이 ID", value: "gw" },
        { text: "Index", value: "indexNm" },
        { text: "Battery", value: "battery" },
        { text: "최근 측정", value: "rcvDtm" },
        { text: "설치일자", value: "initDtm" },  
        { text: "측정간격(min)", value: "term" },  
        { text: "경보 상태(0,1)", value: "alertLvl" },  
        /* 
        { text: "X-팩터", value: "xFactor" },
        { text: "Y-팩터", value: "yFactor" },
        { text: "X-오프셑", value: "xOffset" },
        { text: "Y-오프셑", value: "yOffset" }, 
        */
      ],
      tableItemsGw: [],      
      headersUser: [
        { text: "사용자ID", value: "usrId" },
        { text: "사용자명", value: "usrNm" },
        { text: "비밀번호", value: "pwd" },
        { text: "권한", value: "role" },
        { text: "잠금여부", value: "isLock" },
        { text: "마지막로그인시간", value: "lastLoginDtm" },  
        { text: "", value: "delRow" },  
      ],
      tableItemsUser: [],      
      sensors: ["센서-0G-1201", "센서-0G-1202", "센서-0G-1203", "센서-0G-1204", "센서-0G-1205", "센서-0G-1206"],
      file: null,
      uploadedImage: null,
    };
  },
  computed: {
    moment() {
      return moment;
    }
  },
  methods: {
    ...mapMutations(['setShowMsg']),
    // 센서목록조회
    async fetchSensList(){},
    async saveSensList(){},
    //알림끄기
    turnOffAlarm(){
      for( let item of this.tableItemsGw){
        item.alertLvl = this.alarm ? 0 : 1;
      }
      this.alarm = this.alarm ? 0 : 1;
    },
    // gw조회
    async fetchGwList(){

    },
    // gw 저장
    async saveGwList(){

    },

    editItem(editItem){
      console.log("edit=>",editItem)
      console.log("this.tableItemsGw=>",this.tableItemsGw)
    },
    addUser(){
      console.log("addUser=>")
      this.tableItemsUser.push({role:'USER',isLock:'N'})

    },
    async delRow(item,index){
      console.log("delRow=>",index,item)
      // this.tableItemsUser.splice(index,1);
      if(!item.REG_DTM){
        this.tableItemsUser.splice(index,1);
        return;
        
      }else if( confirm("삭제하시겠습니까?") ){
        this.loading = true;
        await DynamoDBService.delUser(item)
        .then((data)=>{
          console.log("--- delRow OK!",data);
          this.tableItemsUser.splice(index,1);
          this.setShowMsg({isVisible:true,color:'success darken-1',msg:'삭제 되었습니다.'})
        })
        .catch((err)=>{
          console.error(err)
          this.setShowMsg({isVisible:true,color:'error',msg:`오류발생 : ${err.message}`})
        }).finally(()=>{
          this.loading = false;
        })
      }
    },
    async saveUserList(){
      console.log("saveUserList=>",this.tableItemsUser)

      // usrId 중복체크,필수체크 ,  createdAt 필드 없으면 pwd  필수체크
      let emptyChk = this.tableItemsUser.filter(item => !item.usrId || !item.usrNm );
      if(emptyChk.length > 0){
        this.setShowMsg({isVisible:true,color:'error',msg:`사용자 아이디, 사용자명은 필수입니다.`})
        return;
      }
      let dupChk = this.tableItemsUser.map(item => item.usrId).filter((item, index, array) => array.indexOf(item) !== index);
      if(dupChk.length > 0){
        this.setShowMsg({isVisible:true,color:'error',msg:`중복된 사용자 아이디가 있습니다. ${dupChk}`})
        return;
      }
      let emptyPwdChk = this.tableItemsUser.filter(item => !item.createdAt && !item.pwd);
      if(emptyPwdChk.length > 0){
        this.setShowMsg({isVisible:true,color:'error',msg:`비밀번호는 필수입니다.`})
        return;
      }
      
      this.loading = true;
      await DynamoDBService.saveUser(this.tableItemsUser)
      .then((data)=>{
        console.log("--- saveUserList OK!",data)
        this.setShowMsg({isVisible:true,color:'success',msg:'저장 되었습니다.'})
        this.getUserList()
      })
      .catch((err)=>{
        console.error(err)
        this.setShowMsg({isVisible:true,color:'error',msg:`오류발생 : ${err.message}`})
      }).finally(()=>{
        this.loading = false;
      })
    },
    async getUserList(){
      console.log("getUserList=>")
      this.loading = true;
      await DynamoDBService.getUserList()
      .then((data)=>{
        console.log("--- getUserList OK!");
        this.tableItemsUser = [];
        this.tableItemsUser.unshift(...data);
      })
      .catch((err)=>{
        console.error(err)
        this.setShowMsg({isVisible:true,color:'error',msg:`오류발생 : ${err.message}`})
      })
      .finally(()=>{
        this.loading = false;
      })
    },
  },
  mounted() {
    console.log("mounted.")
    let mom = moment();console.log(mom);

    // GW0 0G#C:20230522173755,0,0.0,1,[1201,1202,1203,1204,1205,1206,1207,1208]
    let sensItems = [
                    {node:'Node 1', indexNm:'Crack',battery:'3.5V',rcvDtm:'2023-06-01 13:00:00',base1:'25.0',base2:'35.0',base3:'45.0',factor:0.551,offset:0.12,zeroSet:0},
                    {node:'Node 2', indexNm:'Stains',battery:'3.5V',rcvDtm:'2023-06-01 13:00:00',base1:'25.0',base2:'35.0',base3:'45.0',factor:0.551,offset:0.12,zeroSet:0},
                    {node:'Node 3', indexNm:'Load',battery:'3.5V',rcvDtm:'2023-06-01 13:00:00',base1:'25.0',base2:'35.0',base3:'45.0',factor:0.551,offset:0.12,zeroSet:0},
                    {node:'Node 4', indexNm:'Collapse',battery:'3.5V',rcvDtm:'2023-06-01 13:00:00',base1:'25.0',base2:'35.0',base3:'45.0',factor:0.551,offset:0.12,zeroSet:0},
                    {node:'Node 5', indexNm:'Collapse',battery:'3.5V',rcvDtm:'2023-06-01 13:00:00',base1:'25.0',base2:'35.0',base3:'45.0',factor:0.551,offset:0.12,zeroSet:0},
                    ]
    let gwItems = [
                    {gw:'295', indexNm:'G-1',battery:'5.0V',rcvDtm:'2023-06-01 13:00:00',initDtm:'2023-06-01 13:00:00',term:60,alertLvl:1},
                  ]
    /* let userItems = [
                    {usrId:'slh', usrNm:'소프트랩흄',pwd:'1',role:'ADMIN',lastLoginDtm:'2023-06-01 13:00:00'},
                    {usrId:'test01', usrNm:'테스터01',pwd:'1234',role:'USER',lastLoginDtm:'2023-06-01 13:00:00'},
                  ] */

    // DynamoDBService.getUserList().then((data)=>{console.log("--- getUserList OK!");this.tableItemsUser.unshift(...data);}).catch((err)=>{console.error(err)});
    this.getUserList();

    this.tableItemsSensor.unshift(...sensItems)
    this.tableItemsGw.unshift(...gwItems)
    
  },
};
</script>

<style scoped>

.layout-card {
  margin: 10px 0;
  border-width: 2px !important; /* 두께 변경 */
  border-color: #aaa !important; /* 색상 변경 */
}

.mb-5 {
  margin-bottom: 2rem;
}

/* .compobox {
    border-width: 2px;
    border-color: #aaa !important; 
    color: #aaaa;
    height: 100px;
  } 
  */

  .layout-table {
  width: 100%;
  border: 2px solid #aaa; /* border color and thickness */
  margin: 2px 0;
}

::v-deep .v-data-table__wrapper thead tr th {
  font-size: 16px !important; /* 원하는 크기로 변경하세요 */
}

::v-deep .v-data-table__wrapper tbody tr td {
  font-size: 14px !important;
}

.layout-table td {
  border: 2px solid #aaa; /* cell border color and thickness */
  text-align: center;
  padding: 5px;
}

.compobox {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

</style>
