  // Files 다운로드
  async downloadFile(file) {
    try {

      await axios.get(`${API_ENDPOINT}/files/downloadFile/`+file.id+"/"+file.seq,{ 
        responseType: 'blob' // Blob 형태의 데이터를 받기 위한 설정
      })
      .then(response => {
        // Blob 객체로 변환
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;

        // 서버에서 제공하는 파일명을 사용하거나 새로운 파일명을 지정
        link.setAttribute('download', file.fileName);
        document.body.appendChild(link);
        link.click();
      })

      return true;
    } catch (error) {
      console.error('Failed to fetch downloadFile :', error);
      return [{code:-1,message:error.message}];
    }
  },

