  // 월별 전력 사용량 조회 
  async getHistoryMonPower(yyyymm) {
    try {

      // yyyymm 이 없으면 오늘 날짜로 조회
      if(!yyyymm) yyyymm = moment().format('YYYYMM');

      console.log("---- getHistoryMonth() =>",yyyymm)

      const response = await axios.get(`${API_ENDPOINT}/hist/getHistoryMonPower/`+yyyymm);
      console.log("---- getHistoryMonth() =>",response.data)
      let rtn = this.exchangeKorDate(response.data);
      rtn = this.calculateCurrent(rtn);
      rtn.forEach(element => {
        element.totPowerKw = (element.CURRENT * 220) / 1000;
        element.totPowerKw = (element.totPowerKw).toFixed(1);
        element.totPower = element.CURRENT;
      });

      return rtn;
    } catch (error) {
      console.error('Failed to fetch items:', error);
      return [{code:-1,message:error.message}];
    }
  },

