import DynamoDBService from '@/services/DynamoDBService.js';

export default {
    namespaced: true,
    state: {
        gw: [],
        senesor:[],
    },
    getters: {
        getGw: (state) => state.gw,
        getSensor: (state) => state.senesor,
    },
    mutations: {
        setGw(state, payload) {
            state.gw = payload;
        },
        setSensor(state, payload) {
            state.senesor = payload;
        },
    },
    actions: {
        async fetchGw({ commit }) {
            const response = await DynamoDBService.getGw();
            commit('setGw', response);
        },
        async fetchSensor({ commit }) {
            const response = await DynamoDBService.getSensor();
            commit('setSensor', response);
        },
    },
};