<template>
  <v-container fluid d-flex flex-column align-center justify-center>
    <v-img
      alt="(주) 스마일 내진"
      class="pa-0 ma-0"
      :width="$vuetify.breakpoint.mobile ? '90dvw': '50dvw'"
      height="60vh"
      src="@/assets/smile_logo_dark.png"
      style="object-fit: contain;"
    ></v-img>
      <v-row style="margin-bottom: 10px;">
        <v-col :cols="6">
          <div class="ml-2"><router-link to="/current"><v-btn  color="primary darken-1" class="subtitle-1"><v-icon>mdi-lightning-bolt</v-icon> 전력관리 바로가기</v-btn></router-link></div>
        </v-col>
        <v-col :cols="6">
          <div class="ml-2"><router-link to="/alert"><v-btn  color="primary darken-1" class="subtitle-1"><v-icon>mdi-lightning-bolt</v-icon> 알림 바로가기</v-btn></router-link></div>      
        </v-col>
      </v-row>
    <v-card outlined width="80%">
          <v-card-title>
            <v-row class="secondary darken-1 rounded text-left">
              <v-col>
                <h3 class="ml-2">최근 알림목록</h3>
              </v-col>
              <!-- <v-col :cols="4" class="d-flex justify-end ">            
                    <div class="ml-2"><router-link to="/alert"><v-btn  color="primary darken-1" class="subtitle-1">바로가기</v-btn></router-link></div>
              </v-col> -->
            </v-row>
          </v-card-title>
          <v-card-text>
            <v-data-table dense divider fixed-header :headers="alertHeaders"
              :items="alertItems.filter(item => item.STATUS)" :items-per-page="5"
              :hide-default-footer="true"
              class="elevation-1">
              <template v-slot:[`item.no`]="{ index }">
                <span>{{ index + 1 }}</span>
              </template>
              <template v-slot:[`item.STATUS`]="{ item }">
                <v-chip
                  :color="item.STATUS == 1 ? 'error' : item.STATUS == 4 ? 'success' : item.STATUS == 5 ? 'warning' : item.STATUS == 0 ? '#333' : '#000'"
                  label small @click="showErrorPopup" class="pd-5">
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-if="item.STATUS == 0">OFF</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 1">ON</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 2">단선</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 3">과전류</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 4">온도센서</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 5">위치인식</p>
                  <p :class="{blink : item.BLINK}" class="mt-4 fs-10" v-else-if="item.STATUS == 6">통신오류</p>
                </v-chip>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
    <v-overlay :value="loading">
      <v-progress-circular indeterminate size="80" width="10" color="primary lighten-2"></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapMutations } from "vuex";
import moment from 'moment';  

export default {
  data() {
    return {
      loading: false,
      totMonPower: 0,
      alertHistList: [],
      alertHeaders: [
        { text: "No.", value: "no", align: "center", width: "20%" },
        { text: "일시", value: "createdAt", width: "40%" },
        { text: "알림", value: "STATUS", align: "center", widht: "40%" }
      ],
      alertItems: [],
      totPowerList: [],
      isActive: true,
    };
  },
  computed: {
    // ...mapState('wssMsg',['msg']),
  },
  methods: {
    ...mapMutations(['setShowMsg']),
    getHistoryList() {
      this.alertItems = [];
      this.loading = true;
      const from = moment().subtract(1, 'days').format('YYYY-MM-DD');
      const to = moment().format('YYYY-MM-DD');
      DynamoDBService.getAlertList(from, to).then((res) => {
        this.alertItems = res;
        this.loading = false;
      })
      .catch((err) => {
        this.setShowMsg({isVisible:true,color:'error',msg: err});
        this.loading = false;
      });
    },
    showErrorPopup() {
      let errMsg = "dddd";
      let param = { isErrorMsg: true, color: 'error', msg: errMsg }
      this.setShowMsg(param);
    },
    reload() {
      this.getHistoryList();
    },


  },
  mounted() {
    this.reload();
  }
};
</script>
<style scoped>
.vue-grid-item {
  touch-action: none;
  /* border: 1px solid goldenrod; */
}

.grid-item-background {
  background-color: #1e1e1e;
}

@media (max-width: 1001px) {
  .clock {
    font-size: 3rem !important;
  }
}

.clock {
  font-family: 'Orbitron', sans-serif;
  font-size: 6rem;
  font-weight: 800;
  text-align: center;
  /* 폰트컬러 yellow lighten-1 */
  color: #ffeb3b;

}

@keyframes blink-effect {
  50% {
    opacity: 0;
  }
}

.blink {
  animation: blink-effect 1s step-end infinite;
}

.fs-10 {
  font-size: 1rem !important;
  padding: 10px 0;
}
</style>