<template>
  <v-container fluid secondary class="fill-height" d-flex flex-column align-center justify-center >
    <v-img
      alt="(주) 스마일 내진"
      class="pa-0 ma-0"
      :width="$vuetify.breakpoint.mobile ? '90dvw': '60dvw'"
      height="20vh"
      src="@/assets/smile_logo_dark.png"
      style="object-fit: contain;"
    ></v-img>
    <v-form class="mb-16">
      <v-text-field
        v-model="username"
        label="사용자 아이디"
        name="username"
        type="text"
        placeholder="사용자 아이디"
        style="width: 300px;fontSize:18px"
        required
      ></v-text-field>

      <v-text-field
        v-model="password"
        :type="showPassword ? 'text' : 'password'"
        name="password"
        label="패스워드"
        placeholder="패스워드"
        required
        append-icon="mdi-eye"
        style="width: 300px;fontSize:18px"
        @click:append="showPassword = !showPassword"
      ></v-text-field>

      <v-btn  class="subtitle-1"  color="primary darken-1" x-large width="200" :loading="loading" @click="login">로그인</v-btn>
    </v-form>
  </v-container>
</template>

<script>
import {  mapActions , mapMutations } from 'vuex';
import router from '@/router';
export default {
  data() {
    return {
      username: 'smile',
      password: '',
      showPassword: false,
      loading: false,
    };
  },
  methods: {
    ...mapActions('userInfo', ['fetchUser']),
    ...mapMutations('userInfo', ['setUser']),
    async login() {
      this.loading = true; // 로딩 시작

      try{
        let resData = await this.fetchUser({usrId : this.username , pwd : this.password});
        console.log("로그인 성공? : ",resData);
        if(resData.usrId){
          this.setUser(resData);
          router.replace("/");
        
        }else{
          alert("로그인 아이디 / 비밀번호를 확인 하세요\n"+resData.message);
        }

      }catch(err){
        // console.err(err);
        alert("로그인 오류.",err.message);
      }

      this.loading = false;
    }
  },
};
</script>
