import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/views/common/Login.vue' ;

import LayoutComponent from '../views/common/LayoutComponent.vue';
import AlertMonitor from '../views/AlertMonitor.vue';
import SystemManage from '../views/SystemManage.vue';
import HeaterManage from '../views/HeaterManage.vue';
import Dashboard from '../views/Dashboard.vue';
import FileDownManage from '../views/FileDownManage.vue';
import DataManage from '../views/DataManage.vue';
import CurrentManage from '../views/CurrentManage.vue';


Vue.use(Router);

const store = require('@/store').default;

const router =  new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: '메인화면',
      component: Dashboard,
      meta: {
        menu: true,
        role : ['USER'],
        icon:'mdi-tablet-dashboard'
      },
    },
    {
      path: '/current',
      name: '전력관리',
      component: CurrentManage,
      meta: {
        menu: true,
        role : ['USER'],
        icon:' mdi-lightning-bolt'
      },
    },
    {
      path: '/login',
      name: '로그인',
      component: Login,
      meta: {
        menu: false,
        public : true,
        role : ['USER'],
        // icon:'mdi-monitor-dashboard'
      },
    },
    /* {
      path: '/',
      name: '메인화면',
      component: Dashboard,
      meta: {
        menu: true,
        icon:'mdi-monitor-dashboard'
      },
    }, */
    {  
      path: '',
      name: '데이터 조회',      
      component: LayoutComponent,
      meta: {
        menu: true,
        // icon: 'mdi-hammer-screwdriver',
        role : ['USER'],
        childrenVisible: false // You can use this to handle the visibility of child routes
      },
      children: [        
        {
          path: '/alert',
          name: '알림',
          component: AlertMonitor,
          meta: {
            menu: true,
            icon: 'mdi-alert-circle-outline',
            role : ['USER'],
          },
        },
      ],
    },     
    {  
      path: '',
      name: '자료실',      
      component: LayoutComponent,
      meta: {
        menu: true,
        // icon: 'mdi-hammer-screwdriver',
        role : ['USER'],
        childrenVisible: false // You can use this to handle the visibility of child routes
      },
      children: [       
        {
          path: '/printFile',
          name: '도면 자료목록',
          component: FileDownManage,
          meta: {
            menu: true,
            icon: 'mdi-file-image',
            role : ['USER'],
          },
        },
        {
          path: '/data',
          name: 'DATA 관리',
          component: DataManage,
          meta: {
            menu: true,
            icon: 'mdi-database',
            role : ['USER'],
          },
        },
        // You can add more child routes here
      ],
    },     
    {  
      path: '',
      name: '시스템 관리',      
      component: LayoutComponent,
      meta: {
        menu: true,        
        // icon: 'mdi-hammer-screwdriver',
        childrenVisible: false , // You can use this to handle the visibility of child routes
        role : ['USER'],
      },
      children: [
        {
          path: '/systemManage',
          name: '기본 설정',
          component: SystemManage,
          meta: {
            menu: true,
            icon: 'mdi-cogs',
            role : ['ADMIN'],
          },
        },
        {
          path: '/heaterMange',
          name: '히터 모니터링',
          component: HeaterManage,
          meta: {
            menu: true,
            icon: 'mdi-monitor-dashboard',            
            role : ['USER'],
          },
        },
      ],
    }
    // Add more routes for additional pages
  ],
});


// 로그인 상태 확인 함수 (실제 로그인 상태 확인 로직으로 대체)
function isLoggedIn() {
  // Vuex Store의 getUser getter를 통해 로그인 상태 확인
  const user = store.getters['userInfo/getUser'];

  console.log("--- isLoggedIn :  user =>",user)
 return !!user.usrId ; // user가 있으면 true, 없으면 false 반환
 //return true;
}


router.beforeEach((to, from, next) => {
  console.log("Router beforeEach => from , to ",from.name , to.name)
  
  // If user is going to a public page
  if (to.matched.some(record => record.meta.public)) {
    // If user is already logged in and trying to go to the login page, redirect to the main page
    if (isLoggedIn() && to.path === '/login') {
      console.log("Already Logined")
    
    } else if (from.path === '/login' && to.path === '/login') {
      console.log("Current Login Page.")

    } else {
      next();
    }
  }
  // If user is going to other pages, check for login status
  else {
    if (!isLoggedIn()) {
      console.log("Not Logined. go Login Page...")
      if (from.path !== '/login') {
        next({
          path: '/login',
        });      
      }else{
        console.log("Current Login Page.")
      }
    } else {
      console.log("login OK")
      next();
    }
  }
});


export default router;