import Vue from 'vue';
import Vuex from 'vuex';
import theme from './theme';
import db from './db';
import userInfo from './userInfo';

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        theme,
        //wssMsg,
        db,
        userInfo,
      },
  state: {
    count: 0,
    showMsg:{isVisible:false,color:'success',msg:'완료되었습니다.'}
  },
  mutations: {
    increment(state) {
      state.count++;
    },
    setShowMsg(state,msg){
      state.showMsg = msg;
    }
  },
  actions: {
    increment(context) {
      context.commit('increment');
    },
  },
  getters: {
    getCount: (state) => {
      return state.count;
    },
    getShowMsg: (state) => {
      return state.showMsg;
    },
  },
});
