<template>
  <v-app
    :style="{
      backgroundColor: $vuetify.theme.themes[$vuetify.theme.dark ? 'dark' : 'light'].background,
    }"
  >
    <v-navigation-drawer app v-model="drawer" v-if="getUser.usrId">
      <v-sheet class="d-flex align-center justify-center" height="64" width="100%">
        
          <span
            :style="{
                fontWeight:800,fontSize: $vuetify.breakpoint.mobile?'1.5rem':'1.75rem'
            }"
          >
            (주) 스마일 내진
          </span>
        
        <!-- <span class="pa-0 text-subtitle-1" >(주)케이씨티이엔씨</span> -->
      </v-sheet>
      <v-list nav>

        <v-list-item :key="item.name" v-for="item in menuItems" @click="selectMenu(item)"
          :style="{
                color:  item.name == currentMenu ? '#C5B736' : $vuetify.theme.themes[$vuetify.theme.dark ? 'dark' : 'light'].text,
                backgroundColor: item.name == currentMenu ? currentMenuColor : '',
              }"
        >
            <v-list-item-title
              v-if="!item.children.length"
              :style="{
                color: $vuetify.theme.themes[$vuetify.theme.dark ? 'dark' : 'light'].text,
              }"
            >
            <v-icon>{{ item.icon }}</v-icon> {{ item.name }}
            </v-list-item-title>

          <v-list-group v-if="item.children.length" style="width:100%;margin-left:-8px">
            <template v-slot:activator>
              <v-list-item-content >
                <v-list-item-title 
                  :style="{
                    color: item.name == currentMenu ? '#c5b736' : $vuetify.theme.themes[$vuetify.theme.dark ? 'dark' : 'light'].text,
                  }"
                >
                  <v-icon>{{ item.icon }}</v-icon> {{ item.name }}
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item @click="selectMenu(subItem)" :key="subItem.name" v-for="subItem in item.children"
              :style="{
                color: subItem.name == currentMenu ? '#c5b736' : $vuetify.theme.themes[$vuetify.theme.dark ? 'dark' : 'light'].text,
                backgroundColor: subItem.name == currentMenu ? '#464646' : '',
              }"
            >
              <v-list-item-content>
                <v-list-item-title class="ml-4">
                  <v-icon>{{ subItem.meta.icon }}</v-icon> {{ subItem.name }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>

        </v-list-item>        

      </v-list>
    </v-navigation-drawer>

    <v-app-bar app>
      <v-btn icon @click="drawer = !drawer">
        <v-icon>mdi-menu</v-icon>
      </v-btn>
      <span v-if="!$vuetify.breakpoint.mobile"
        :style="{
          color: $vuetify.theme.themes[$vuetify.theme.dark ? 'dark' : 'light'].text,fontSize:'1.25rem'
        }"
      >
        {{ currentMenu }}
      </span>
      <v-spacer></v-spacer>
      <span
        v-if="!$vuetify.breakpoint.mobile"
        :style="{
          fontWeight:800,fontSize: $vuetify.breakpoint.mobile?'':'1.75rem'
        }"
      >
        수계배관 스마트 열선 관리 모니터링 시스템
      </span>
      <v-spacer></v-spacer>
      <span v-if="false" class="clock mr-4">
        {{ currTime }}
      </span>
      <v-btn v-if="false" @click="toggleDarkMode">
        {{ darkMode ? 'Light Mode' : 'Dark Mode' }}
      </v-btn>
      <v-btn v-if="getUser.usrId" color="yellow" small outlined @click="logout">{{getUser.usrNm}} 로그아웃</v-btn>
    </v-app-bar>

    <v-main ref="vMain" style="textAlign:center;height:100%; width: 100%;">
      <vuescroll :ops="scrollOptions" >
        <router-view></router-view>
      </vuescroll>
    </v-main>
<!--
    <v-snackbar
      :timeout="2000"
      v-model="showWssSnackbar"
      color="error"
      variant="tonal"
      size="large"
      @click="showWssSnackbar = false"
    > -->
      <!-- <template v-slot:activator="{ props }">
        <v-btn color="primary" variant="tonal" class="ma-2" v-bind="props">open</v-btn>
      </template> -->

   <!--   <strong>서버와의 센서정보 수신 연결이 중단되어 재연결 중입니다 . . .</strong>
    </v-snackbar> -->
    <!--<v-snackbar
      :timeout="2000"
      v-model="showWssSucc"
      color="success"
      variant="tonal"
      size="large"
      @click="showWssSucc = false"
    > -->
      <!-- <template v-slot:activator="{ props }">
        <v-btn color="primary" variant="tonal" class="ma-2" v-bind="props">open</v-btn>
      </template> -->

    <!--  <strong style="font-color:red">센서로 부터 차트정보가 업데이트 되었습니다.</strong>
    </v-snackbar> -->
    <v-snackbar
      right
      :timeout="3000"
      v-model="getShowMsg.isVisible"
      :color="getShowMsg.color"      
      @click="getShowMsg.isVisible = false"
      :class="{ shake: getShowMsg.isVisible }"
    >
      <!-- <template v-slot:activator="{ props }">
        <v-btn color="primary" variant="tonal" class="ma-2" v-bind="props">open</v-btn>
      </template> -->

      <strong style="font-color:red;text-align: center;display: block;">{{getShowMsg.msg}}</strong>
    </v-snackbar>

    <!-- getShowMsg.isErrorMsg 가 true 인경우 error 배경컬러의 에러메시지를 팝업으로 보여준다. -->
    <!-- <v-dialog v-model="getShowMsg.isErrorMsg"  max-width="250px">
      <v-card color="error" >
        <v-card-title class="headline"></v-card-title>
        <v-card-text class="text-center font-weight-bold" style="font-size: 1.25em;color: white;" v-html="getShowMsg.msg.replace(/\n/g, '<br>')">
          {{getShowMsg.msg}}
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="secondary" dense small  @click="getShowMsg.isErrorMsg = false">확인</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog> -->
  </v-app>
</template>

<script>
import { mapMutations, mapGetters , mapActions } from 'vuex';
import router from '@/router';
import vuescroll from "vuescroll";

//const WS_URL = process.env.VUE_APP_WEBSOCKET_URL;
//const WS_PORT = process.env.VUE_APP_WEBSOCKET_PORT;

export default {
  name: 'App',
  components: {
    vuescroll,
  },
  data() {
    return {
      drawer: true,
      currentMenu: '메인화면',
      currentMenuColor:'',
      menuItems: [],
      scrollOptions: {
        // Add your scroll options here
      },
      currTime:"",
      showWssSucc:false,
      showWssSnackbar:false,
    };
  },
  computed: {
    ...mapGetters(['darkMode','getShowMsg']),
    ...mapGetters('userInfo',['getUser']),
    // ...mapGetters('db', ['getGw']),
  },
  watch: {
    darkMode(newVal) {
      this.$vuetify.theme.dark = newVal;
    },
    getUser(newVal){
      console.log("--- whatcher!!! getUser.role =>",newVal.role)
      this.menuItems = this.getMenuItems();
    },
  },
  methods: {
    ...mapMutations(['toggleDarkMode','setDarkMode']),
    //...mapMutations('wssMsg', ['addWssMsg','setWssIsConn']),
    ...mapMutations('userInfo', ['setUser']),
    ...mapActions('db', ['fetchGw','fetchSensor']),
    selectMenu(menuItem) {
      
      console.log( JSON.stringify( menuItem ));

      this.currentMenuColor = "";

      let hasChild = menuItem.children || [] ;
      if( "" === menuItem.path || hasChild.length )  {        
        this.toggleChildrenVisibility(menuItem);
        return false;
      }

      if (this.$route.path !== menuItem.path) {
        this.currentMenu = menuItem.name;
        this.currentMenuColor = "#464646";
        router.replace(menuItem.path);
      }
    },
    toggleChildrenVisibility(item) {
      let chirenVis = item.meta.childrenVisible || null;      
      if (item.meta && null != chirenVis ) {
        item.meta.childrenVisible = !item.meta.childrenVisible;
      }
    },
    getMenuItems() {
      const items = [];
      router.options.routes.forEach((route) => {        

        let menuRole = route.meta.role[0] || "USER"
        let userRole = this.getUser.role || "USER"

        console.log("--- getMenuItems =>",JSON.stringify(route.meta),menuRole,userRole)
        if (route.meta && route.meta.menu && (userRole === "ADMIN" || menuRole === userRole) ) {
          const item = {
            name: route.name,
            path: route.path,
            icon: route.meta.icon,
            meta : route.meta,
            // childrenVisible : ( route.meta.childrenVisible || false )
          };
          if (route.children) {            
            item.children = route.children.filter(childRoute => childRoute.meta && childRoute.meta.menu && (userRole === "ADMIN" || childRoute.meta.role[0] === userRole));
            // console.log("--- item.children=>",item.children)
          }else{
            item.children = [];
          }
          items.push(item);
          console.log("--- item =>",JSON.stringify(item))
        }
      });
      return items;
    },
    updateTime() {
      const now = new Date();
      this.currTime = now.toLocaleString();
    },
   /* wssConn(){
      // 연결할 웹소켓 서버 주소를 아래에 넣으세요.
      const wsUrl = `ws://${WS_URL}:${WS_PORT}`;
      //const wsUrl = 'wss://echo.websocket.org';
      this.socket = new WebSocket(wsUrl);

      this.socket.onopen = () => {
        console.log("@ Connected to WebSocket server");
      };
      this.socket.onmessage = (message) => {
        // 웹소켓으로부터 메시지를 받아 Vuex store에 추가
        this.showWssSucc = true;
        this.addWssMsg(message.data);
        this.setWssIsConn(true);
      };

      this.socket.onclose = (e) => {
        console.log('Socket is closed. Reconnect will be attempted in 1 second.', e.reason);
        this.setWssIsConn(false);
        this.showWssSnackbar = true;
        setTimeout(() => {
          this.wssConn();
        }, 1000);
      };

      this.socket.onerror = function(err) {
        console.error('Socket encountered error: ', err.message, 'Closing socket');
        if(this.socket) this.socket.close();
      };
    },*/
    logout(){
      let isLogout = confirm("로그아웃 하시겠습니까?");
      if( isLogout ) {
        this.setUser({});
        router.replace("/login");
      }
    },
  },
  created() {
    this.$vuetify.theme.dark = true;
    this.menuItems = this.getMenuItems();
    // this.selectMenu(this.menuItems[0]);
    
  },
  mounted() {        
    this.updateTime();
    this.intervalId = setInterval(this.updateTime, 1000);
    // 웹소켓 연결
   // this.wssConn();

    console.log("# getShowMsg =>",this.getShowMsg);    
    console.log("# getUser =>",this.getUser);

  },
  beforeDestroy() {
    clearInterval(this.intervalId);
    // 컴포넌트가 사라질 때 웹소켓 연결을 종료
   // this.socket.close();
  },
};
</script>
<style scoped>
.v-main {
  overflow: hidden;
  height: 100%;
}

.vuescroll {
  width: 100%;
  height: 100%;
}


::v-deep .__view {
    height: -webkit-fill-available;
  }

.v-main-scrollbar .vb-scrollbar {
  width: 8px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.v-main-scrollbar .vb-scrollbar:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.v-main-scrollbar .vb-scrollbar.vb-draggable {
  background-color: rgba(0, 0, 0, 0.5);
}
.v-list-item__title  {
  font-weight: 700 !important;
  font-size: 18px
}
.v-toolbar__content {
  font-weight: 700 !important;
  font-size: 18px
}
.v-application {
    /* font-family: 'Noto Sans Korean', 'sans-serif' !important; */
    font-family: 'NanumSquare', 'sans-serif' !important;
    font-weight: 400 !important;
    font-size: 14px
  }

  .v-application .text-subtitle-1 {
    /* font-family: 'Noto Sans Korean', 'sans-serif' !important; */
    font-family: 'NanumSquare', 'sans-serif' !important;
    font-weight: 700 ;
    /* color: darkgoldenrod; */
  }

@media (max-width: 767px) {
  .clock {font-size: 0.1em;}
}
.clock {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  width: 350px; /* 너비를 고정하고자 하는 값으로 설정하세요 */
  text-align: right;
  
}


::v-deep .v-list .v-list-item--active, .v-list .v-list-item--active  {
  color: #fff !important;
}

@keyframes shake {
  0% { transform: translate(0.5px, 0.5px) rotate(0deg); }
  10% { transform: translate(-0.5px, -1px) rotate(-1deg); }
  20% { transform: translate(-1.5px, 0px) rotate(1deg); }
  30% { transform: translate(1.5px, 1px) rotate(0deg); }
  40% { transform: translate(0.5px, -0.5px) rotate(1deg); }
  50% { transform: translate(-0.5px, 1px) rotate(-1deg); }
  60% { transform: translate(-1.5px, 0.5px) rotate(0deg); }
  /* 70% { transform: translate(1.5px, 0.5px) rotate(-1deg); }
  80% { transform: translate(-0.5px, -0.5px) rotate(1deg); }
  90% { transform: translate(0.5px, 1px) rotate(0deg); }
  100% { transform: translate(0.5px, -1px) rotate(-1deg); } */
}

.shake {
  animation: shake 0.7s; 
  /* animation-iteration-count: infinite; */
   animation-iteration-count: 1;
}

</style>
