<template>
  <v-container fluid>    
    <v-row>
      <v-col>
            <v-card outlined class="mt-5">
              <v-card-title>        
                <v-row class="secondary darken-1 rounded text-left">
                  <v-col :cols="8" >
                    <h3 class="ml-2">당월 전체 전력량</h3>
                  </v-col>
                  <v-col :cols="4" class="d-flex justify-end ">            
                    <div class="ml-2"><v-btn  color="primary darken-1" class="subtitle-1" :loading="loading" @click="reload">🔄 새로고침</v-btn></div>
                  </v-col>
                </v-row>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col class="mt-8" >                
                    <span class="clock">
                      {{ totMonPower }} kW
                    </span>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
    </v-row>
    <v-row class="justify-center">
      <!-- 좌측 넓이 50%  , 높이100% 영역으로 월별 전력량 기준 차트 -->
      <v-col>
        <v-card outlined class="mt-5">
          <v-card-title>        
            <v-row class="secondary darken-1 rounded text-left">
              <v-col :cols="12" >
                <h3 class="ml-2">월별 차트</h3>
              </v-col>
            </v-row>
          </v-card-title>
          <apexchart
            class="mt-10"
            type="bar"
            :options="{
              chart: {
                height: 350,
                type: 'bar',
                background: '#1e1e1e',
                toolbar: {
                  show: false,
                }
              },
              plotOptions: {
                bar: {
                  borderRadius: 4,
                  horizontal: false,
                }
              },
              colors: ['#18c7c1', '#18c7c1','#18c7c1'],
              dataLabels: {
                enabled: true,
                style:{
                  fontSize: '30px'
                }
              },
              grid: {
                borderColor: '#FEB019',
              },
              title: {
                text: '월별 전력량(kW)',
                align: 'left',
                style: {
                  color: '#FFFFFF',
                },
              },
              markers: {
                size: 0,
              },
              tooltip: {
                enabled: false,
                theme: 'dark',
                style: {
                  fontSize: '14px',
                  colors: ['#ffffff'],
                }
              },
              xaxis: {
                // categories: ['Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct'],
                // categories: totPowerList에서 yyyy/mm 포맷으로 추출하여 배열로 만들기
                // categories: totPowerList.map(item => item.yyyymm),
                categories: totPowerList.map(item => `${item.yyyymm.substring(0,4)}/${item.yyyymm.substring(4,6)}`),
                labels: {
                  style: {
                    colors: '#FFFFFF',
                    fontSize: 18,
                  },
                },
              },
              yaxis: {
                labels: {
                  style: {
                    colors: '#FFFFFF',
                  },
                },
              },
            }"
            :series="[
           //   {
           //     name: '전월 전력 사용량(kW)',
                // data: [30, 40, 45, 50, 49, 60, 70, 91, 125]
                // data: totPowerList에서 totPower 추출하여 배열로 만들기
           //     data: totPowerList.map(item => item.CURRENT),
           //   },
              {
                //name: '당월 전력 사용량(kW)',
                // data: [30, 40, 45, 50, 49, 60, 70, 91, 125]
                // data: totPowerList에서 totPower 추출하여 배열로 만들기
                data: totPowerList.map(item => item.CURRENT),
              }
            ]"
            
          ></apexchart>
        </v-card>
      </v-col>
    </v-row>
    
          
        
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="80"
        width="10"
        color="primary lighten-2"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import {mapMutations} from "vuex";
import moment from 'moment';  


export default {  
  data() {
    return {
      loading: false,
      totMonPower : 0,
      alertHistList : [],
      alertHeaders: [
        { text: "No.", value: "no" , align: "center",width:"20%"},
        { text: "일시", value: "createdAt" ,width:"40%"},
        { text: "알림" ,value:"STATUS" , align: "center" ,widht:"40%"}
      ],
      alertItems: [],
      totPowerList :[],
      isActive: true,
    };
  },
  computed: {
   // ...mapState('wssMsg',['msg']),
  },
  watch: {    
    selectedFloor(newVal) {
      // 선택된 층의 floorId가 변경되면, 해당 층의 정보를 찾아 selectedFloorInfo에 저장
      this.getHeaterList(newVal);
    },
  },
  methods: {    
    ...mapMutations(['setShowMsg']),    
    getHistoryList(count){
        this.alertItems = [];
        this.loading = true;

        // 최근 count개 데이터를 조회 /getHistory/:count
        DynamoDBService.getHistoryCount(count).then((res) => {
          this.alertItems = res;
          this.loading = false;
        })
        .catch((err) => {
          console.log(err);
          this.loading = false;
        }).finally(() => {
          this.updateCharts();
        });
        
    },
    getHistMonthPower(yyyymm){
      this.loading = true;
      let param = yyyymm || moment().format('YYYYMM');
      console.log("날짜 >>>>>>>>>>>> " + param);
      DynamoDBService.getHistoryMonPower(param).then((res) => {
        
        this.totMonPower = res.find(item => item.yyyymm == param).CURRENT;
        this.totPowerList = res;
        this.loading = false;
      })
      .catch((err) => {
        console.log(err);
        this.loading = false;
      }).finally(() => {
        this.updateCharts();
      });
    },
    updateCharts(){

    },
    
    showErrorPopup() {
      let errMsg = "dddd";
      let param = {isErrorMsg:true,color:'error',msg: errMsg}
      this.setShowMsg(param);
    },
    reload(){
      this.getHistoryList(5);
      this.getHistMonthPower();
    },

    
  },
  mounted() {
    this.reload();
  }
};
</script>
<style scoped>

.vue-grid-item  {
  touch-action: none;
  /* border: 1px solid goldenrod; */
}
.grid-item-background {
  background-color: #1e1e1e;
}

@media (max-width: 1001px) {
.clock {font-size: 3rem !important; }
}
.clock {
  font-family: 'Orbitron', sans-serif;
  font-size: 6rem;
  font-weight: 800;  
  text-align: center; 
  /* 폰트컬러 yellow lighten-1 */
  color: #ffeb3b;

}

@keyframes blink-effect {
  50% {
    opacity: 0;
  }
}
.blink {
  animation: blink-effect 1s step-end infinite;
}
.fs-10{
  font-size: 1rem !important;
  padding: 10px 0;
}
</style>